<template>
  <Popover ref="refPopover" v-bind="$props">
    <template #default>
      <slot></slot>
    </template>
    <template #reference>
      <slot name="reference"></slot>
    </template>
  </Popover>
</template>

<script>
import { defineComponent, onMounted, ref } from 'vue'
export default defineComponent({
  setup() {
    const refPopover = ref(null)
    onMounted(() => {
      refPopover.value?.$refs?.popperRef?.classList?.remove?.('is-light')
      refPopover.value?.$refs?.popperRef?.classList?.add?.('is-dark')
    })
    return {
      refPopover
    }
  }
})
</script>
